/* Home.css */

.background {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f7f6f3;

}




.introduction {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.my-self {
  max-width: 600px;
}



.self-description {
  font-size: 1rem;
  color: #000a09;
  line-height: 1.5;
  letter-spacing: 0rem;
  max-width: 10000px;
  font-weight: lighter;
  animation: typing 1s steps(40) forwards; /* Add typing animation */
  font-family: monospace;
  margin-left: 15rem;
}
.position {
color: rgb(47, 235, 9);
margin-top: 2rem;
font-weight: bold;
font-family: monospace;
font-size: 1.5rem;
margin-left: 11rem;

}
 
 .abt {
  color: #0d0e0d;
  background-color: #ffffff;
  margin-right: 1rem;
  height: 3rem;
  width: 7rem;
  font-size: 17px;
  margin-top: 1rem ;
  border-radius: 5px 5px;
  
}
.abt:hover{
  color: #f5e6e6;
} 
@keyframes typing {
  0% {
    width: 0; /* Start with no width (hidden text) */
  }
  100% {
    width: 100%; /* End with full width (fully revealed text) */
  }
}

.image {
  margin-left: 26rem;
  margin-top: 6rem;
}

.hero-img {
  width: 500px;
  height: 619px;
  flex-shrink: 0;
  position: relative; /* Ensure the image respects the parent's positioning */
  z-index: 1; /* Set the image to be on top */
}
.eclipse{
width: 450px;
height: 500px;
flex-shrink: 0;
border-radius: 54% 46% 0% 100% / 100% 42% 58% 0%;
background: linear-gradient(180deg, #070622 0%, rgba(217, 217, 217, 0.00) 100%);
margin-left: 3rem;
margin-top: -31rem;
z-index: 0;
position: absolute; /* Position the eclipse absolutely within the parent */

}


/* Media query for screens up to 768px width (typical phone screens) */
@media (max-width: 768px) {
  .self-description {
    font-size: 1rem;
    color: #295751;
    line-height: 1.5;
    letter-spacing: 0rem;
    max-width: 300px;
    margin: 0 auto;
    font-weight: lighter;
    animation: typing 1s steps(40) forwards; /* Add typing animation */
  }
  
  @keyframes typing {
    0% {
      width: 0; /* Start with no width (hidden text) */
    }
    100% {
      width: 100%; /* End with full width (fully revealed text) */
    }
  }
  
 .introduction {
    margin-top: 2rem;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-left: 1rem;
    width: 50%;
  }
 

  .self-description {
    font-size: 14px;
    text-align: justify;   
    margin-top: 3rem;
    margin-left: 13rem;
  }

  .hero-img {
   display: none;

}
.eclipse {
  display: none;
}
.img {
  display: none;
}
.abt {
  color: #0c0c0c;
  background-color: #f7f8f8;
  margin-left: 13rem;
  margin-top: 1rem;
  height: 3rem;
  width: 6rem;
}
.abt:hover{
  color: #0f0d0d;
  transition: transform 0.2s;
}
.position {
  margin-left: 10rem;
  font-size: 1rem;
}
}