.macbook-pro a::after {
  content: '\2192'; /* Unicode arrow right character */
  position: absolute;
  top: 50%; /* Vertically center the arrow */
  left: 5px; /* Adjust the left position as needed */
  opacity: 0; /* Initially hide the arrow */
  transition: opacity 0.3s; /* Add a transition for a smooth hover effect */
}

/* Show the arrow icon on hover */
.macbook-pro a:hover::after {
  opacity: 1;
}











